
*,
*::before,
*::after {
    box-sizing: border-box;
}
html {
    font-size: 100%;
}
body {
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;

  /* font info has to be applied to body so it applies to mouseovers and date picker */
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.window {
  left: 8px;
  top: 64px;
  width: 420px;
  height: 290px; /* 248 - is golden ratio of 420 + 42px for the header */
  box-shadow: 0 2px 4px rgba(0,0,0,.2), 0 -1px 0 rgba(0,0,0,.02);
}
a.primary-action {
  color: #ff4081;
}
a {
  color: #4183C4;
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  color: #ff4081;
}
.visualization {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#searchForm {
  padding: 0 14px;
  position: absolute;
  white-space: nowrap;
  background: white;
  box-shadow: 0 2px 4px rgba(0,0,0,.2), 0 -1px 0 rgba(0,0,0,.02);
  top: 8px;
  left: 8px;
  width: 420px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#searchString {
  height: 56px;
  padding-left: 10px;
  font-size: 18px;
  outline: none;
  border: none;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#searchString:focus {
  border: none;
}
#startSearch {
  color: #4183C4;
  background: transparent;
  border: none;
  padding: 0;
  font-size: 14px;
  cursor: pointer;
  margin-left: 4px;
}
#startSearch:hover {
  color: #ff4081;
}
#header {
    text-align: center;
    background: #6DAEE1;
    position: absolute;
    left: 0;
    width: 100%;
    height: 28px;
    color : white;
}
.footer {
  position: absolute;
  width: 100%;
  bottom : 9px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#copyText {
  position: absolute;
  left: 10px;
  bottom: 9px;
  font-size: 9px;
  color: #999;
}
#about {
  position: absolute;
  left: 8px;
  padding: 14px;
  top: 64px;
  width: 420px;
  background: white;
  box-shadow: 0 2px 4px rgba(0,0,0,.2), 0 -1px 0 rgba(0,0,0,.02);
}
.about-link {
  background: rgba(255, 255, 255, 0.8);
  padding: 0 14px;
}
.close-about {
  float: right;
}
.log-message {
  position: absolute;
  bottom: 42px;
  width: 100%;
  text-align: center;
  color: #ff4081;
  padding: 4px;
  background: rgba(255, 255, 255, 0.8);
}
@media only screen and (max-width: 600px) {
#searchForm {
    width: 100%;
    top: 0;
    left: 0;
}
.window {
    left: 0;
    top: 56px;
    height: 35vh;
    width: 100%;
}
#searchString {
    font-size: 14px;
}
#about {
    left: 0;
    top: 56px;
    width: 100%;
}
.footer {
    padding-right: 14px;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
}
}
.heart {
  color: #ff4081;
}



